@use '../util' as *;

:root {
  --sizing-parameter: max(100vw, 100vh);
  --sketch-info-width: rem(256);
  --sketch-info-height: rem(128);
}

.container {
  padding: 0 0;
  // max-width: 100vh;
  margin: 0 0 0 rem(32);
  display: block;
  align-content: center;
  position: absolute;

  &__body {
    padding: 0;
    top: rem(96);
    width: calc(100vw - rem(32));
    height: calc(100vh - rem(96));
  }
}

.sketch_container {
  position: absolute;
  top: 0;
  bottom: 0;
  display: block;
  z-index: -1;
  width: 100vw;
  height: 100vh;
  margin: 0;
  overflow: hidden;
  border: none;

  &__frame {
    position: relative;
    top: rem(-42);
    bottom: 0;
    display: block;
    width: 100vw;
    height: calc(100vh + rem(42));
    border: none;
  }
}

.link_tree {
  padding: rem(17) rem(16);
  position: fixed;
  top: rem(128);
  display: grid;
  width: rem(301);
  height: rem(184);
  left: rem(48);
  grid-template-columns: rem(32) rem(210);
  grid-template-rows: repeat(3, 1fr);
  align-items: center;
  gap: rem(27);
  background: hsla(0, 100%, 0%, 0.6);
  border-radius: rem(10);

  &__icon {
    position: relative;
    display: flex;
    width: rem(32);
    height: rem(32);
    align-items: center;
    text-align: center;
    justify-self: end;
    color: white;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    justify-self: start;
    color: white;
  }
}

.sketch_info {
  position: absolute;
  padding: rem(17) rem(16);
  width: rem(256);
  height: rem(128);
  left: calc(50vw - rem(32) - (0.5 * rem(256)));
  // top: calc(50vh - rem(130) - (0.5 * rem(128)));
  bottom: rem(32);
  background: hsla(0, 100%, 0%, 0.6);
  border-radius: rem(10);
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  // justify-content: space-between;
  text-align: center;
  color: white;

  @include breakpoint-down(medium) {
    bottom: rem(112);
    transition: bottom 80ms ease-in-out;
  }

  @include breakpoint(medium) {
    bottom: rem(32);
    transition: bottom 80ms ease-in-out;
  }

  &__title {
    height: rem(24);
    visibility: inherit;
  }

  &__subtitle {
    height: rem(32);
    visibility: inherit;
  }

  &__description {
    text-wrap: wrap;
    height: rem(24);
    visibility: inherit;
  }
}

.copyright_box {
  position: fixed;
  bottom: rem(32);
  left: rem(32);
  display: grid;
  width: rem(256);
  height: rem(48);
  grid-template-columns: rem(32) rem(136);
  grid-template-rows: 1fr;
  gap: rem(4);
  background: hsla(0, 100%, 0%, 0.6);
  align-items: top;
  justify-content: center;
  border-radius: rem(10);

  &__icon {
    width: rem(32);
    height: rem(58);
    padding-top: rem(2);
    justify-self: end;
    font-size: rem(48);
    color: white;
  }

  &__item {
    padding-top: rem(15);
    justify-self: start;
    color: white;
  }

  &__button {
    position: absolute;
    overflow: hidden;
    background: none;
    width: 100%;
    height: 100%;
    border: none;
    border-radius: rem(10);
    transition: background-color 80ms ease-in-out;
    z-index: 1;
  
    &:hover {
      background: hsla(0, 100%, 100%, 0.2);
    }
  }
}
