@use '../util' as *;

.header {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: rem(32) rem(32);
  align-items: center;

  &__logo {
    width: rem(64);
    height: rem(64);
    border: rem(2) solid black;

    &--button {
      position: absolute;
      width: rem(64);
      height: rem(64);
      background: none;
      border: none;
      transition: background-color 80ms ease-in-out;
      z-index: 1;
    
      &:hover {
        background: hsla(0, 100%, 100%, 0.2);
      }
    }
  }

  &__title {
    width: 100%;
    height: 100%;
    font-size: rem(32);
    font-weight: 700;
    -webkit-text-stroke-width: rem(1.5);
    -webkit-text-stroke-color: black;
    color: var(--text-color);
    margin-left: rem(24);
    text-shadow: rem(2) rem(2) rem(4) hsla(0, 100%, 0%, 0.75);

    &--button {
      position: absolute;
      left: rem(88);
      width: rem(228);
      height: rem(32);
      background: none;
      border: none;
      border-radius: rem(6);
      transition: background-color 80ms ease-in-out;
      z-index: 1;

      &:hover {
        background: hsla(0, 100%, 100%, 0.2);
      }
    }
  }

  &__subtitle {
    font-size: rem(14);
    font-weight: 700;
  }
}